import { AUTOCOMPLETE_STYLES, PANEL_PRIMARY_STYLE } from "assets/base-theme";
import primaryLoader from "assets/img/common/Loading.svg";
import searchIcon from "assets/img/common/search_icon.svg";
import BaseAutoComplete, { IBaseAutoCompleteProps } from "components/base/BaseAutoComplete";
import { IInputCommonStyleProps } from "components/interface/Input";
import React from "react";
import { generateClasses } from "utils/Util";

interface IAutoCompleteBoxProps extends IBaseAutoCompleteProps, IInputCommonStyleProps {
  panelStyleObj?: object;
  loader?: boolean;
}

const AutoComplete: React.FC<IAutoCompleteBoxProps> = (props: IAutoCompleteBoxProps) => {
  const getClassNames = () => {
    interface STYLE_OBJECT {
      primaryStyle: object;
      secondaryStyle?: object;
      tertiaryStyle?: object;
      quaternaryStyle?: object;
      errorStyle?: object;
      overrideStyle?: object;
      overrideClasses?: string;
      disabledStyleProperty?: string[];
      removeStyleProperty?: string[];
      removeClasses?: string[];
      isDisabled?: boolean;
      applyErrorStyle?: boolean;
      [key: string]: any; // additional property Allowed (for ternaryStyle or Other)
    }
    const styleObject: STYLE_OBJECT = {
      ...AUTOCOMPLETE_STYLES,
      overrideStyle: props.styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: props.validation ? true : false,
      removeStyleProperty: [],
      removeClasses: [],
    };

    const classNamesObject = generateClasses(styleObject, props.variant);

    const panelStyleObject: STYLE_OBJECT = {
      primaryStyle: PANEL_PRIMARY_STYLE,
      overrideStyle: props.panelStyleObj,
      removeStyleProperty: [],
      removeClasses: [],
    };

    const panelClassNamesObject = generateClasses(panelStyleObject, props.variant);

    return [classNamesObject, panelClassNamesObject];
  };

  const getClassNameValues = getClassNames();
  const classNames = getClassNameValues[0];
  const panelClassNames = getClassNameValues[1];

  const {
    value,
    onChange,
    name,
    placeholder,
    label,
    validation,
    onSelect,
    showPanel,
    loader,
    hideLabel,
    emptyMessage,
    showEmptyMessage,
  } = props;

  return (
    <div className="relative w-full">
      <BaseAutoComplete
        {...props}
        className={classNames}
        value={value}
        onChange={onChange}
        name={name}
        placeholder={placeholder}
        label={label}
        validation={validation}
        panelClassName={panelClassNames}
        onSelect={onSelect}
        showPanel={showPanel}
        labelClassNames={props.labelClassNames ? props.labelClassNames : " mb-1 "}
        emptyMessage={emptyMessage}
        showEmptyMessage={showEmptyMessage}
      />
      {loader ? (
        <div className="absolute w-4 h-4 right-3 top-1/2 transform -translate-y-1/2 flex justify-center items-center pointer-events-none cursor-pointer">
          <img src={primaryLoader} className="animate-spin" />
        </div>
      ) : (
        <></>
      )}
      <div className={hideLabel ? "absolute top-1.5 left-2" : "absolute top-7 left-2"}>
        <img src={searchIcon} alt="search-icon" className="max-w-fit h-2.5 w-2.5" />
      </div>
    </div>
  );
};

// memoziation

export default React.memo(AutoComplete);
// redux wiring
