import InputError from "components/InputError";
import Label from "components/Label";
import { withAuth } from "components/hoc/withAuth";
import { IBaseInputCommonProps } from "components/interface/Input";
import { AutoComplete, AutoCompleteProps } from "primereact/autocomplete";
import React, { useEffect, useRef } from "react";

export interface IBaseAutoCompleteProps extends AutoCompleteProps, IBaseInputCommonProps {
  showPanel?: true;
}

const BaseAutoComplete: React.FC<IBaseAutoCompleteProps> = (props: IBaseAutoCompleteProps) => {
  const {
    hideLabel,
    hideErrorRow,
    label,
    validation,
    labelClassNames,
    errorClassNames,
    disabled,
    isDisabledByAcl,
    showPanel,
    showEmptyMessage,
    emptyMessage,
    reportFormDirty,
    ...PrimeReactProps
  } = props;

  const { placeholder, className, value, style, name, onChange } = PrimeReactProps;

  //Refs
  const autoCompleteRef: any = useRef();

  //Initial start
  useEffect(() => {
    if (showPanel === true) {
      autoCompleteRef?.current?.showOverlay();
    }
  }, [props]);

  return (
    <div className="w-full group grid">
      {!hideLabel && <Label label={label} labelClassNames={labelClassNames} />}

      <AutoComplete
        appendTo="self"
        {...PrimeReactProps}
        className="w-full"
        inputClassName={`${className} ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor border-primary-gray-300 placeholder-secondary-gray-500 !border-primary-gray-300"
            : " "
        } `}
        value={value}
        style={style}
        onChange={onChange}
        name={name}
        placeholder={placeholder}
        ref={autoCompleteRef}
        disabled={disabled || isDisabledByAcl}
        showEmptyMessage={showEmptyMessage}
        emptyMessage={emptyMessage}
      />
      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </div>
  );
};

export default withAuth(BaseAutoComplete);
